<template>
  <a-row type="flex" justify="end">
    <a-col style="width: 85%">
      <a-row type="flex" justify="start">
          <a-col :span="1" offset="1">
            <router-link to="/idManger">
            <a-button type="link"><a-icon type="left" />返回</a-button>
            </router-link>
          </a-col>
        </a-row>
      <a-card style="width: 90% ;margin: 50px auto" :bordered="true"
              :style="{backgroundImage: 'url(' + img + ')',
          backgroundRepeat:no-repeat,center,top,
          backgroundSize:'100% 100%'
          }">
        <div class="main user-layout-register">
          <a-row type="flex" justify="center" style="margin-top: 100px; margin-bottom: 100px">
            <a-col :span="7">
              <h3 style="text-align: center">
                <span>添加管理员</span>
              </h3>
              <a-form ref="formRegister" :form="form" id="formRegister">
                <a-form-item>
                  <a-input
                    size="large"
                    type="text"
                    placeholder="用户名"
                    v-decorator="['username', {rules: [{ required: true, message: '请输入用户名' }], validateTrigger: ['change', 'blur']}]"
                  >
                  <a-icon slot="prefix" type="user" :style="{ color: 'rgba(0,0,0,.25)' }"/>
                  </a-input>
                </a-form-item>

                <a-form-item>
                  <a-input
                    size="large"
                    type="password"
                    autocomplete="false"
                    placeholder="密码"
                    v-decorator="['password', {rules: [{ required: true, message: '至少6位密码，区分大小写'}, { validator: this.handlePasswordLevel }], validateTrigger: ['change', 'blur']}]"
                  >
                  <a-icon slot="prefix" type="lock" :style="{ color: 'rgba(0,0,0,.25)' }"/>
                  </a-input>
                </a-form-item>

                <a-form-item>
                  <a-button
                    size="large"
                    type="primary"
                    htmlType="submit"
                    class="register-button"
                    :loading="registerBtn"
                    @click="handleSubmit"
                    :disabled="registerBtn"
                  >添加</a-button>
                </a-form-item>
              </a-form>
            </a-col>
          </a-row>
        </div>
      </a-card>
    </a-col>
  </a-row>
</template>

<script>
import { addManager } from "@/api/manRequest";

export default {
  name: "Register",
  components: {},
  data() {
    return {
      form: this.$form.createForm(this),

      state: {
        time: 60,
        smsSendBtn: false,
        percent: 10,
        progressColor: "#FF0000"
      },
      registerBtn: false,
      img:require('../../assets/dddb-webEnt/outbg.jpg')
    };
  },
  computed: {},
  created() {
    this.$parent.showSiderMenu = true;
    this.$parent.showMenu = false;
  },
  methods: {
    handleSubmit() {
      const {
        form: { validateFields },
        state
        // $router
      } = this;
      validateFields({ force: true }, (err, values) => {
        if (!err) {
          const registerParams = { ...values };
          const that = this;
          addManager(registerParams)
          .then(function(response) {
            window.console.log(response);
            if (response.data.result === 'true') {
              alert("添加成功");
              that.$router.push({ path: "/idManger" });
            } else if (response.data.result === 'false') {
              alert("管理员用户名已存在，请重新输入");
            }
          })
          .catch(err => {
              console.log(err)
          });
        }
      });
    }
  },
  watch: {}
};
</script>
<style lang="less">
.user-register {
  &.error {
    color: #ff0000;
  }

  &.warning {
    color: #ff7e05;
  }

  &.success {
    color: #52c41a;
  }
}

.user-layout-register {
  .ant-input-group-addon:first-child {
    background-color: #fff;
  }
}
</style>
<style lang="less" scoped>
.user-layout-register {
  & > h3 {
    font-size: 16px;
    margin-bottom: 20px;
  }

  .register-button {
    width: 100%;
  }

  .login {
    float: right;
    line-height: 40px;
  }
}
</style>




